<template>
  <h2 :id="title" class="category">
    <span>{{ title }}</span>
    <button @click="dropOrUp()">
      <img class="close" src="@/assets/img/dropArrow.svg" alt="dropArrow" :style="{transform: rotate}">
    </button>
  </h2>
</template>

<script>
import {useStore} from "vuex";
import {onMounted, ref} from "vue";

export default {
  name: "CategoryTitle",
  props: ['title'],
  setup(props) {
    const store = useStore()
    const rotate = ref('rotate(180deg)')

    onMounted(() => {
      store.commit('main/setCategoryOffset', document.querySelector('#' + props.title).offsetTop)
    })

    const dropOrUp = () => {
      for(const [key, value] of Object.entries(store.state.main.itemArr)) {
        if(value.category === props.title) {
          value.isOpen = !value.isOpen
          rotate.value = value.isOpen ? 'rotate(180deg)' : 'rotate(0)'
          break
        }
      }
    }

    return { dropOrUp, rotate }
  }
}
</script>

<style scoped>
h2 {
  width: 100%;
  background: #FFAB6F;
  color: #ffffff;
  font-size: 4.5vw;
  display: flex;
  justify-content: space-between;
  box-shadow: #00000020 0 2px 4px;
  padding: 2vw 3vw 2vw 6vw;
}

.close {
  transform: rotate(180deg);
}
</style>