import axios from "axios";

const coupon = {
  namespaced: true,
  state: {
    couponList: []
  },
  mutations: {
    setCouponList: (state, list) => {
      state.couponList = list
    }
  },
  actions: {
    getCoupon: (context) => {
      axios
        .get('/dummyData/couponData.json')
        .then(res => {
          context.commit("setCouponList", {...res.data})
        })
    }
  }
}

export default coupon