import { createRouter, createWebHashHistory } from 'vue-router'
import MainContainer from "@/views/MainContainer";
import CartContainer from "@/views/CartContainer";
import LoginContainer from "@/views/LoginContainer";
import FinishOrderContainer from "@/views/FinishOrderContainer";
import MyCouponsContainer from "@/views/MyCouponsContainer";
import OrderListLogin from "@/views/OrderListLogin";
import OrderListContainer from "@/views/OrderListContainer";

const routes = [
  {
    path: "/:id",
    component: MainContainer,
  },
  {
    path: "/:id/cart",
    component: CartContainer
  },
  {
    path: "/:id/login",
    component: LoginContainer
  },
  {
    path: "/:id/finish",
    component: FinishOrderContainer
  },
  {
    path: "/coupon/:userId",
    component: MyCouponsContainer
  },
  {
    path: "/:id/orderLogin",
    component: OrderListLogin
  },
  {
    path: "/:id/OrderList",
    component: OrderListContainer
  }
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
