<template>
  <button @click="goCoupon">쿠폰 확인하기</button>
</template>

<script>
import {useRoute, useRouter} from "vue-router";

export default {
  name: "GoToCoupon",
  setup() {
    const router = useRouter()
    const route = useRoute()

    const storeId = route.params.id

    const goCoupon = () => {
      router.push(`/${storeId}/coupon`)
    }

    return { goCoupon }
  }
}
</script>

<style scoped>
button {
  background: #FFAB6F;
  color: #ffffff;
  width: 80vw;
  font-size: 5vw;
  margin: 10px auto;
  display: block;
  border-radius: 8px;
  padding: 1vw 0;
}
</style>