<template>
  <div class="wrap">
    <button :class="anima" @click="goToCart">장바구니 {{store.state.main.hasCart}}개 상품</button>
  </div>
</template>

<script>
import {useRoute, useRouter} from "vue-router";
import {useStore} from "vuex";
import {ref, watch} from "vue";

export default {
  name: "GoToCart",
  setup() {
    const router = useRouter()
    const route = useRoute()
    const store = useStore()

    const anima = ref('')

    const goToCart = () => {
      const storeId = route.params.id
      router.push(`/${storeId}/cart`)
    }

    watch(() => store.state.main.hasCart, () => {
      anima.value = 'animation'
      setTimeout(() => {
        anima.value = ''
      }, 1000)
    })

    return { goToCart, store, anima }
  }
}
</script>

<style scoped>
.wrap {
  position: fixed;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  padding: 2vw 5vw;
  width: 100%;
  background: linear-gradient(to top, #00000030, #00000000);
}

button {
  display: block;
  width: 100%;
  background: #FFAB6F;
  color: #ffffff;
  border-radius: 8px;
  font-size: 4vw;
  padding: 2vw 0;
  text-align: center;
  position: relative;
  left: 0;
}

.animation {
  animation: vibe .5s 1 ease-in;
}

@keyframes vibe {
  0% {left: 5px;}
  20% {left: -5px;}
  40% {left: 5px;}
  60% {left: -5px;}
  80% {left: 5px;}
  100% {left: 0;}
}
</style>