<template>
  <div class="container">
    <h1>{{ storeName }}</h1>
    <div>주문 관리</div>
    <input type="password" placeholder="비밀번호" v-model="pw">
    <span class="warning" v-if="warning">비밀번호를 잘못 입력하셨습니다.</span>
    <button @click="login">로그인</button>
  </div>
</template>

<script>
import {useStore} from "vuex";
import {ref} from "vue";
import {useRoute, useRouter} from "vue-router";

export default {
  name: "OrderListLogin",
  setup() {
    const store = useStore()
    const router = useRouter()
    const route = useRoute()
    const storeName = 'Mega'
    const pw = ref('')
    const warning = ref(false)

    const login = () => {
      if(pw.value === '1234') {
        router.push(`/${route.params.id}/OrderList`)
      } else {
        warning.value = true
        pw.value = ''
      }
    }

    return { storeName, pw, login, warning }
  }
}
</script>

